
body{
  width: 100%;
  min-width: 320px;
  max-width: 750px;
  background: #fff;
  margin: 0 auto;
}
a{
  text-decoration: none;
  color: #7f7f7f;
}
.zhong{
  font-weight: bold;
  color: #333;
}
.zhe{
  position: absolute;
  width: 100%;
  max-width: 750px;
  height: 100%;
  background: #000;
  filter:alpha(opacity=30);
  opacity: 0.3;
}
#app {
  width: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  background: #fff;
}
#app ul li{
  list-style-type:none;
  float:left;
}
#app #topNav{
  width: 100%;
  max-width: 750px;
  overflow: hidden;
}
#app #navs{
  width:87%;
  height: 0.88rem;
  float:left;
}
#app #navs>li{
  /*width: 20%;*/
  height: 0.88rem;
  line-height: 0.88rem;
  font-size: 0.3rem;
  text-align:center;
  padding: 0 0.2rem
}
#app #sou{
  width:13%;
  height:0.88rem;
  float:left;
  background: url(./assets/fang.png) left center no-repeat;
  background-size: 0.32rem 0.32rem;
}
#app #sou input{
  float:left
}
#app #lun{
  width: 100%;
  height: 3.6rem;
  overflow: hidden;
}
#app #nav2s{
  width:100%;
  height:0.88rem;
  position: relative;
}
#app #nav2s>li{
  width:25%;
  height: 0.88rem;
  font-size:0.26rem;
  text-align:center;
  line-height:0.88rem;
  color: #818181;
}
#app #nav2s>li>img{
  width: 0.20rem;
  height: 0.11rem;
  text-align: top;
  margin-left: 0.1rem;
  margin-bottom: 0.03rem;
}
#app #nav2s>li>div{
  position: absolute;
  background: #fff;
  width: 100%;
  left: 0;
  z-index: 10;
}
#app #nav2s>li>div>p{
  text-align: left;
  padding-left: 0.5rem;
}
#app #nav2s>li>div>p:nth-of-type(n+1){
  border-top: 1px solid #ccc;
}



