@charset "utf-8";
/**{margin:0;padding:0}*/
body{font-size:13px;font-family:arial;-webkit-text-size-adjust:none;}
table{border-collapse:collapse;border-spacing:0;}
h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:normal;}
ul,ol,li{list-style:none;}
em,i{font-style:normal;}
img{border:none;}
input,img{vertical-align:middle;}
a{color:#666;text-decoration:none;-webkit-tap-highlight-color:rgba(0,0,0,0);/* 去掉链接触摸高亮 */}
a:active{color:#333;-webkit-tap-highlight-color:rgba(0,0,0,0);}
section,aside,header,footer,hgroup,article{display:block}

/*html{font-size:1em;-webkit-tap-highlight-color:rgba(0,0,0,0);-webkit-tap-highlight:rgba(0,0,0,0);-webkit-text-size-adjust:none;}*/
html{-webkit-tap-highlight-color:rgba(0,0,0,0);-webkit-tap-highlight:rgba(0,0,0,0);-webkit-text-size-adjust:none;}
/*body{font-size:0.75em;}*/
a:link, a:visited {text-decoration:none;}
/*input,button,select,textarea{outline:none;}*/
/*label{cursor:pointer;}*/
/*textarea{resize:none;}*/
table th, table td {border:0;}
a, abbr, acronym, address, applet, article, aside, audio, b, blockquote, big,
body, center, canvas, caption, cite, code, command, datalist, header, hgroup,
dd, del, details, dfn, dl, div, dt, embed,
fieldset, figcaption, figure, font, footer, form,
h1, h2, h3, h4, h5, h6,  html,
i,em, iframe,
img, ins, kbd, keygen, label,
legend, li, meter, nav, menu,
object, ol, output, p, pre, progress,
q, s, samp, section, small, span, source, strike,
strong, sub, sup, table, tbody, tfoot, thead, th, tr, tdvideo, tt,
u, ul, var {margin:0;padding:0;}

/*article, aside, footer, header, hgroup, nav, section, figure, figcaption{display:block;}*/

/*h1, h2, h3, h4, h5, h6, th, td, table, input, button, select, textarea, sub{font-size:1em;}*/
body, input, button, select, textarea, sub{ font-family:Arial, sans-serif;}
em, cite, address, optgroup {font-style:normal;}
kbd, samp, code {font-family:monospace;}

img, input, button, select, textarea {vertical-align:middle;}

img, fieldset {border:0;}
abbr, acronym {cursor:help; border-bottom:1px dotted black;}
table {width:100%;border-spacing:0;border:0}




