
.h5{
  width: 100vw;
  max-width: 750px;
  min-width: 320px;
  overflow: hidden;
  background: #ededed;
  /*min-height: 50vh;*/
  padding-bottom: 0.2rem;
  position: relative;
}
.uiR{
  width: 94%;
  max-width: 705px;
  min-width: 300.8px;
  height: 2.4rem;
  margin: 0.2rem auto 0;
  overflow: hidden;
  background: #fff;
}
.uiTop{
  width: 100%;
  height: 1.4rem;
  overflow: hidden;
  padding-top: 0.2rem;
}
.uiLeft{
  width: 1.25rem;
  height: 1.25rem;
  background: #ccc;
  border-radius: 50%;
  margin: 0 0.3rem;
  float: left;
  overflow: hidden;
}
.uiLeft img{
  width: 100%
}
.uiRight{
  width: 5rem;
  height: 1.25rem;
  float: left;
}
.uiMsg{
  width: 100%;
  overflow: hidden;
}
.uiMsg li{
  width: 5rem;
  height: 0.4rem;
  margin-top: 0.05rem;
}
.uiMsg li:nth-of-type(1) span{
  font-size: 0.3rem;
  color: #333;
}
.uiMsg li:nth-of-type(1) span:nth-of-type(1){
  margin-right: 0.14rem;
}
.uiMsg li:nth-of-type(1) span:nth-of-type(3){
  float: right;
  color: #ff7e00
}
.uiMsg li:nth-of-type(2) span{
  font-size: 0.26rem;
  color: #868686;
  margin-right: 0.4rem;
}
.uiMsg li:nth-of-type(2) span img{
  width: 0.2rem;
  height: 0.2rem;
  margin-right: 0.1rem;
  margin-bottom: 0.07rem;
}
.uiMsg li:nth-of-type(3) img{
  margin-right: 0.1rem;
}
.uiMsg li:nth-of-type(3) .img1{
  width: 0.42rem;
  height: 0.24rem;
}
.uiMsg li:nth-of-type(3) .img2{
  width: 0.29rem;
  height: 0.24rem;
}
.uiBottom{
  height: 0.4rem;
  padding: 0.2rem 0;
  overflow: hidden;
}
.uiBottom>span{
  display: inline-block;
  padding: 0.1rem 0.3rem;
  background: #ddf4f3;
  height: 0.2rem;
  line-height: 0.2rem;
  font-size: 0.2rem;
  float: left;
  margin-left: 0.2rem;
  color: #a1a1a1;
}



